@import "../../../styles/variables.css";
.listItem {
  /* margin-bottom: 40px;
  padding-left: 20px; */
}
.listItem * {
  box-sizing: border-box;
}
.card {
  height: 205px;
  width: 256px;
  cursor: pointer;
  box-shadow: 0 0 5px rgb(0 0 0 / 20%);
  position: relative;
  transition: box-shadow 0.2s ease-in;
  display: block;
}

.card:focus-within {
  outline: 2px solid var(--prime-color-primary);
  outline-offset: 2px;
}
.card.hover {
  box-shadow: 0 5px 10px rgb(0 0 0 / 20%);
}

.card.hover .bottomBar {
  bottom: 0;
}

.card.hover .descriptionContainer {
  display: flex;
  background-color: var(--prime-color-white);
}

.card.hover .trainingType {
  padding-bottom: 10px;
  margin-bottom: 0;
}

.card.hover .extra {
  display: none;
}

.card.hover .backdrop {
  background-color: rgba(0, 0, 0, 0.65);
}
.card.hover .topBar {
  opacity: 0.2;
}

.card.hover .showOnHover {
  display: block;
}

.detailsContainer {
  z-index: 1;
  background: transparent;
}

.topBar {
  position: absolute;
  width: 100%;
  letter-spacing: 0px;
  color: var(--prime-color-white);
  opacity: 1;
  font-size: var(--prime-font-size-12);
  padding: 15px 15px 15px 10px;
}
.format {
  width: 50%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-transform: uppercase;
  letter-spacing: 0;
  background: none;
  cursor: pointer;
}

.bottomBar {
  position: absolute;
  width: 100%;
  height: auto;
  padding: 0px 10px;
  color: var(--prime-color-white);
  bottom: 0;
}

.price {
  align-items: center;
  justify-content: center;
  background: var(--prime-color-white);
  border-radius: 12px;
  font-size: var(--prime-font-size-12);
  color: var(--prime-color-black);
  font-weight: bold;
  padding: 3px 8px;
}
.title {
  margin: 8px 0;
  letter-spacing: 0px;
  opacity: 1;
  font-size: var(--prime-font-size-18);
  line-height: 24px;
  text-transform: capitalize;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.trainingType {
  font-size: var(--prime-font-size-10);
  line-height: 13px;
  letter-spacing: 0px;
  opacity: 1;
  text-transform: capitalize;
  margin-bottom: 20px;
}
.thumbnail {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.lillsLabel {
  font-size: var(--prime-font-size-10);
  font-weight: bold;
  line-height: 11px;
  letter-spacing: 0px;
  color: var(--prime-color-black);
}
.lillsLabel::after {
  content: ":";
}
.lillsValue {
  font-size: var(--prime-font-size-10);
  line-height: 11px;
  letter-spacing: 0px;
  color: var(--prime-color-black);
  padding-left: 5px;
}

.backdrop {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.75) 100%
  );
}
.extra {
  width: 100%;
  height: 30px;
  bottom: 0;
  left: 0;
  font-size: var(--prime-font-size-16);
  padding: 0 10px;
  background: rgba(255, 255, 255, 0.8);
  position: absolute;
  justify-content: space-between;
  align-items: center;
  display: flex;
}
.extraWrapper {
  display: flex;
  align-items: center;
  width: 100%;
}
.extraLabel {
  font-size: var(--prime-font-size-10);
  font-weight: bold;
  letter-spacing: 0px;
  color: var(--prime-color-black);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 80%;
}
.extraLabel span {
  font-weight: normal;
  padding-left: 5px;
  font-size: var(--prime-font-size-12);
}
.extraLabel span:first-child:after {
  content: ":";
}

.progressBarContainer {
  width: 100%;
  height: 6px;
  background: var(--prime-color-nc5);
  border-radius: 0.5rem;
}
.progressBar div[class^="spectrum-BarLoader-fill"] {
  /* width: 0;
  height: 100%;
  border-radius: 0.5rem; */
  /* transition: width 750ms ease-in-out;
  -webkit-transition: width 750ms ease-in-out; */
  background: linear-gradient(90deg, #55b36f, #0f870f);
}
.skillsContainer {
  display: flex;
  align-items: center;
}
.descriptionContainer {
  flex-direction: column;
  justify-content: center;
  padding: 10px;
  margin-left: -10px;
  margin-right: -10px;
  color: var(--prime-color-black);
}

.skillsContainer {
  display: flex;
  align-items: center;
  display: flex;
  font-size: var(--prime-font-size-12);
}
.skillsContainer div {
  width: 50%;
}
.percentComplete {
  padding-left: 10px;
}

.description {
  font-size: var(--prime-font-size-10);
  line-height: 13px;
  overflow: hidden;
  letter-spacing: 0px;
  color: var(--prime-color-nc1);
  z-index: 2;
  margin: 0 0 10px 0;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.skiillsLabel {
  font-size: var(--prime-font-size-10);
  font-weight: bold;
  line-height: 11px;
  letter-spacing: 0px;
  color: var(--prime-color-black);
}
.skiillsLabel::after {
  content: ":";
}
.skillsValue {
  font-size: var(--prime-font-size-10);
  line-height: 11px;
  letter-spacing: 0px;
  color: var(--prime-color-black);
  padding-left: 5px;
}
.completed {
  font-size: var(--prime-font-size-12);
  line-height: 13px;
}

.showOnHover {
  display: none;
}
.sendIcon {
  fill: rgba(0, 0, 0, 0.4);
  width: 25px;
  height: 25px;
}

.extraIcon {
  margin-left: auto;
  width: 24px;
  height: 24px;
  fill: rgba(0, 0, 0, 0.4);
}

@media screen and (max-width: 414px) {
  .listItem {
    width: 100%;
    padding: 0;
    /* margin-bottom: 30px; */
  }
  .card {
    width: 100%;
    height: 300px;
  }
}
